/* CSS Reset */
/*  1. Use a more-intuitive box-sizing model. */
*, *::before, *::after {
  box-sizing: border-box;
}

/*  2. Remove default margin  */
* {
  margin: 0;
}

/*  3. Allow percentage-based heights in the application */
html, body {
  height: 100%;
}

/*
  Typographic tweaks!  4. Add accessible line-height  5. Improve text rendering */
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

/*  6. Improve media defaults */
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

/*  7. Remove built-in form typography styles */
input, button, textarea, select {
  font: inherit;
}

/*  8. Avoid text overflows */
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/*  9. Create a root stacking context */
#root, #__next {
  isolation: isolate;
}

/* CSS variables */
:root {
  --primary-orange: hsl(25, 97%, 53%);
  --neutral-white: hsl(0, 0%, 100%);
  --neutral-light-grey: hsl(217, 12%, 63%);
  --neutral-med-grey: hsl(216, 12%, 54%);
  --neutral-dark-blue: hsl(213, 19%, 18%);
  --neutral-very-dark-blue: hsl(216, 12%, 8%);
  --ff-body: "Overpass", sans-serif;
  --fw-400: 400;
  --fw-700: 700;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

/* Global styles */
:focus {
  outline: none;
}

/* Center card using Grid */
body {
  display: grid;
  place-content: center;
  background-color: hsl(0deg, 0%, 0%);
  color: var(--neutral-light-grey);
  font-family: var(--ff-body);
  font-weight: var(--fw-400);
  font-size: 0.9375rem;
}

.ratings-card, .thank-you-card {
  background-color: var(--neutral-very-dark-blue);
  padding: 1rem;
  border-radius: 1rem;
  max-width: 21rem;
}

.div--icon-star {
  background-color: var(--neutral-dark-blue);
  padding: 1rem;
  border-radius: 50%;
  height: 3rem;
  width: 3rem;
  margin-block: 1rem;
}

h1 {
  color: var(--neutral-white);
  font-weight: var(--fw-700);
  padding-block: 0.5rem;
}

p {
  color: var(--neutral-med-grey);
}

.button--rating {
  border-radius: 50%;
  border: none;
  background-color: var(--neutral-dark-blue);
  color: var(--neutral-light-grey);
  text-align: center;
  padding: 0.7rem 1rem 0.5rem 1rem;
  margin-right: 1rem;
  margin-block: 1.5rem;
  cursor: pointer;
}

.button--rating:hover {
  background-color: var(--neutral-light-grey);
  color: var(--neutral-white);
}

.button--rating:focus {
  background-color: var(--primary-orange);
  color: var(--neutral-white);
}

.submit-button {
  text-transform: uppercase;
  font-weight: var(--fw-700);
  letter-spacing: 0.2rem;
  color: var(--neutral-white);
  background-color: var(--primary-orange);
  padding: 0.7rem 6.5rem;
  border: none;
  border-radius: 10rem;
  cursor: pointer;
  margin-bottom: 1rem;
}

.submit-button:hover,
.submit-button:focus {
  background-color: var(--neutral-white);
  color: var(--primary-orange);
}

.thank-you-card {
  display: none;
  text-align: center;
  transition: transform 1s;
}

.div--selection-icon {
  padding: 2rem 4rem;
}

/* Display typescript variable for rating */
.div--selected {
  background-color: var(--neutral-dark-blue);
  border: none;
  color: var(--primary-orange);
  border-radius: 10rem;
  padding: 0.5rem;
  margin-bottom: 1rem;
}

#rating--output {
  padding-right: 0.2rem;
}

.attribution {
  padding-top: 2rem;
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228deg, 45%, 44%);
}

/* Expand card on desktop */
@media (min-width: 56.25rem) {
  .ratings-card, .thank-you-card {
    max-width: 25rem;
    padding-left: 2rem;
  }
  .button--rating {
    margin-right: 1.5rem;
  }
  .submit-button {
    padding: 0.9rem 8rem;
    margin-top: 0.7rem;
  }
}
/* Adjust padding and margins for smallest screens */
@media (max-width: 20.625rem) {
  .ratings-card, .thank-you-card {
    padding-left: 0.5rem;
  }
  .button--rating {
    margin-right: 0.3rem;
  }
  .submit-button {
    padding: 0.5rem 5rem;
  }
}/*# sourceMappingURL=index.css.map */